import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { SET_DATA_API } from 'store/actions';
import { useHistory } from 'react-router-dom';

// material-ui
import { Grid, Link, Chip, Typography, CardContent, Card, Box } from '@material-ui/core';

// project imports
import SubCard from 'ui-component/cards/SubCard';
import MainCard from 'ui-component/cards/MainCard';
import SecondaryAction from 'ui-component/cards/CardSecondaryAction';
import APICard from 'ui-component/cards/Skeleton/APICard';

import { gridSpacing } from 'store/constant';
import configData from 'config';
import { makeStyles, useTheme } from '@material-ui/styles';
import axios from 'axios'
//==============================|| TYPOGRAPHY ||==============================//

const APIMarketplace = () => {
    const [isLoading, setLoading] = useState(true);
    const [dataset, setDataset] = useState([{}]);
    const theme = useTheme();
    let history = useHistory();
    const account = useSelector((state) => state.account);
    // const apis = useSelector((state) => state.apicards);
    const dispatcher = useDispatch();

    const dataSetter = (list) => {
        setDataset(list);
        dispatcher({ type: SET_DATA_API, data: list });
    }

    const refreshAPIs = () => {
        const url1 = configData.API_SERVER+"marketplace";
        axios.get(url1, { headers: { Authorization: `${account.token}` } })
          .then((response) => dataSetter(response.data))
          .then((j) => setLoading(false))
    }

    useEffect(() => {
        // setDataset(apis.data);
        // if (apis.data.length > 1){
        //     setLoading(false);
        // }
        refreshAPIs();
    }, []);
    

      const handleSubscribe = (id) => {
        const url = configData.API_SERVER+"users/subscribe";
        axios.post(url, {}, { headers: { Authorization: `${account.token}` }, params: {api_id: id} })
          .then((response) => console.log(response))
          .then(() => refreshAPIs())
      }

      const handleUnsubscribe = (id) => {
        const url = configData.API_SERVER+"users/unsubscribe";
        axios.post(url, {}, { headers: { Authorization: `${account.token}` }, params: {api_id: id} })
          .then((response) => console.log(response))
          .then(() => refreshAPIs())
      }

    return (
        <MainCard title="Developer API Marketplace">
            <Grid container spacing={gridSpacing}>
                {isLoading ? <Grid item xs={12} sm={6}><SubCard><APICard/></SubCard></Grid> : <></>}
                {!isLoading ? dataset.map((item, index) => (
                <Grid item xs={12} sm={6} key={index+''}>
                    <SubCard
                        title={
                            <Grid container>
                                <Grid item sm={6}>
                                    <Typography variant="h2" sx={{ color: theme.palette.secondary.dark }}>
                                        {item.title}
                                    </Typography>
                                </Grid>
                                <Grid item sm={6}>
                                    <Box display="flex" justifyContent="flex-end">
                                    {!item.subscribed ? 
                                    <Chip label="Subscribe" variant="outlined" onClick={() => handleSubscribe(item.id)} />
                                    : <Chip label="Subscribed" onClick={() => handleUnsubscribe(item.id)}/>
                                    }
                                    </Box>
                                </Grid>
                            </Grid>
                        }
                        onClick={()=>{history.push("/utils/apis/"+item['id'])}}
                        sx={{cursor: "pointer"}}>
                        <Grid item>
                            {/* <Card>
                                <CardContent sx={{backgroundColor: theme.palette.secondary.light}}>
                                    <Grid item container alignItems="center" justifyContent="space-between">
                                    </Grid>
                                </CardContent>
                            </Card> */}
                            <Typography variant="subtitle1">
                                {item.url}
                            </Typography>
                            <Typography variant="body1" gutterBottom>
                                {item.description}
                            </Typography>
                        </Grid>
                    </SubCard>
                </Grid>
                )) : <></>}
            </Grid>
        </MainCard>
    );
};

export default APIMarketplace;
